import { PIXEL_TO_TILEMAP_POSITION, PIXEL_NAME } from '../../utils/constants'
import Destroyable from './Destroyable'

export default class Ground extends Destroyable {
  constructor(id, level) {
    super({
      id,
      isRigid: true,
      isBreakable: true,
      isFlat: true,
      isPickable: false,
      isCrushable: false,
      level
    })
    this.strength = 1
  }

  overrunBy(el) {
    super.overrunBy(el)
    // assume is player who overruns ground
    const layer = this.level.scene.children.getByName('layer_1')
    const { tilemap: map } = layer
    
    map.putTileAt(PIXEL_TO_TILEMAP_POSITION[PIXEL_NAME.EMPTY], this.position.x, this.position.y)
    this.level.scene.events.emit('sounds:clear_hit')

    // we need to run this after level.objects updates from level.moveObject.
    // so we queue it for the next tick
    this.level.onNextTick(level => {
      // ** calcultate tile shape for each neighbour of removed tile **
  
      // cross neighbours coords offset (up, right, down, left)
      const neighboursCoords = [[0, -1], [1, 0], [0, 1], [-1, 0]]
      neighboursCoords.forEach(coords => {
        // create a 3x3 matrix to pass to calculateGroundTileWithNeighbours
        const data = Array(3).fill(0).map(() => Array(3).fill(0))
  
        // gets object from neighbour coordinate
        const x = this.position.x + coords[0]
        const y = this.position.y + coords[1]
        const neighbour = level.getObjectAt(x, y)
        
        // check if neighbour and it is a ground
        if (neighbour && neighbour.constructor.className === this.constructor.className) {
          // build data based on current neighbour, setting ground pixel or 0 accordingly.
          data[0][1] = level.getObjectAt(x, y-1)?.constructor.className === this.constructor.className ? PIXEL_NAME.GROUND : 0
          data[1][0] = level.getObjectAt(x-1, y)?.constructor.className === this.constructor.className ? PIXEL_NAME.GROUND : 0
          data[2][1] = level.getObjectAt(x, y+1)?.constructor.className === this.constructor.className ? PIXEL_NAME.GROUND : 0
          data[1][2] = level.getObjectAt(x+1, y)?.constructor.className === this.constructor.className ? PIXEL_NAME.GROUND : 0
  
          // gets and updates tile
          const tile = PIXEL_TO_TILEMAP_POSITION.calculateGroundTileWithNeighbours(1, 1, data)
          map.putTileAt(tile, x, y)
        }
      })
    })
  }
}

Ground.className = 'Ground'
